const styles = theme => ({
    icon: {
        fontSize: '100px',
        margin: 'auto',
        color: '#3f51b5'
    },
    heroUnit: {
        backgroundColor: "transparent",
        margin: 20
    },
    cardContainer: {
        maxWidth: 1000,
        margin: '0 auto',
    },
    heroContent: {
        maxWidth: 600,
        margin: '0 auto',
        padding: `${theme.spacing.unit * 2}px 0 ${theme.spacing.unit * 6}px`,
    },
    heroButtons: {
        marginTop: theme.spacing.unit * 4,
    },
    cardGrid: {
        padding: `${theme.spacing.unit * 8}px 0`,
    },
    card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        backgroundColor: 'transparent',
        boxShadow: 'none'
    },
    cardContent: {
        flexGrow: 1,
    },
    resetLinkStyle: {
        textDecoration: 'none',
        color: 'white'
    }
});

export default styles;